body {
  font-family: "Hack", monospace;
  color: #000;
  background-color: #e6e6e6; }

.gridTest {
  position: fixed;
  top: 0;
  /* display: none; */
  z-index: 100;
  width: 100%;
  pointer-events: none; }

.gridTest__col {
  height: 100vh;
  border: 1px solid rgba(255, 0, 255, 0.1); }
  .gridTest__col:after {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    background: rgba(255, 165, 0, 0.1); }

@media screen and (max-width: 500px) {
  .gridTest__col:nth-child(2) {
    width: 10%; }
  .gridTest__col:nth-child(1n + 3) {
    display: none; } }

.lib__section {
  margin-top: 2rem;
  margin-bottom: 2rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
  background-color: #fff;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2); }

.lib__section__headline {
  margin-bottom: 2rem;
  font-size: 1.2rem;
  font-weight: bold;
  color: #a0a4a3;
  text-align: center;
  text-transform: uppercase;
  border-bottom: 1px solid #e6e6e6; }

.lib__element {
  margin-top: 1rem;
  margin-bottom: 2rem; }

.lib__element__name {
  font-family: "Hack", monospace;
  font-weight: bold;
  color: firebrick; }

.lib__element--layoutBlock {
  color: #fff;
  background-color: darkgrey; }

.lib__element--colorSwatch {
  display: inline-block;
  width: 20%;
  height: 150px;
  margin-left: 10%;
  padding: .5rem;
  font-family: monospace;
  font-weight: bold;
  border: 10px solid rgba(255, 255, 255, 0.4);
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3); }
  .lib__element--colorSwatch--primaryLink {
    color: #fff;
    background-color: #f25405; }
  .lib__element--colorSwatch--primaryContent {
    color: #fff;
    background-color: #000; }
  .lib__element--colorSwatch--primaryBG {
    background-color: #fff; }
  .lib__element--colorSwatch--darkBG {
    color: #fff;
    background-color: #6d7170; }
  .lib__element--colorSwatch--lightBG {
    background-color: #e6e6e6; }
  .lib__element--colorSwatch--lightContent {
    background-color: #a0a4a3; }

.lib__element--type,
.lib__element__module {
  font-family: "NeueHaas", sans-serif;
  color: #000; }
